.search-form {
  width: 350px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

ul {
  padding: 0;
  list-style: none;
}

.gallery {
  display: inline-block;
}

input {
  color: #333;
  background-color: #eee;
  border: 2px solid #ccc;
  padding: 10px;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more-hidden {
  display: none;
}

.load-more, .seach-button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.btn-load {
  text-align: center;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.photo-card {
  width: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.info {
  text-align: center;
  gap: 10px;
  padding: 15px;
  display: flex;
}

.info p {
  font-size: 12 px;
  color: #000;
  margin: 5px 0;
}

.info-item {
  line-height: 1.5;
}

